@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,100&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.test {
  color: red;
}
.album-tracks {
    padding-top: 15vh;
    padding-bottom: 15vh;
}

.track {
    border: 1px solid white;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 5%;
    padding-right: 5%;
    margin-top: 5px;
    margin-bottom: 5px;
    margin-left: 20%;
    margin-right: 20%;
    color: #dddddd;
    cursor: pointer;
}

.track:hover {
    border: 1px solid #d9e00b;
    color: #d9e00b;
}

.text-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.back-btn-to-lib:hover {
    background-color: #d9e00b;
    opacity: 0.7;
    color: black;
    transition: all 0.3s;
}

.overlay {
  position: fixed;
  width: 100vw;
  background-color: black;
  opacity: 0.9;
  height: 100vh;
  z-index: 2;
}

.hide {
  margin-top: 100vh;
  transition: margin 0.5s;
}

.show {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: scroll;
  margin-top: 0vh;
  animation: slide-up 0.75s;
}

@keyframes slide-up {
  from {margin-top: 100vh;}
  to {margin-top: 0vh;}
}


.welcome {
  background: rgba(27, 24, 27, 0.9);
  z-index: 0;
  height: 17vh;
  display: flex;
  justify-content: left;
  align-items: flex-end;
}

.welcome h1 {
  margin-top: 0px;
  margin-bottom: 1vh;
  margin-left: 1vh;
  font-size: 4rem;
  font-family: 'Roboto', sans-serif;
  color: #d9e00b;
  font-weight: 100;
}
.fade-in {
  animation: fadeIn ease 2s;
}
  
@keyframes fadeIn {
0% {opacity:0;}
100% {opacity:1;}
}
.card {
  cursor: 'pointer';
}

.background {
  background-color: rgba(0, 0, 0, 0.5)
}

.album-list {
  z-index: 0;
}

.animate {
  position: absolute;
  top: 20%;
  animation: slide-up-albums 1s;
}

@keyframes slide-up-albums {
  from {margin-top: 100%;}
  to {margin-top: 0%;}
}
.App {
  text-align: center;
}

.body {
  background: black;
}

.particles {
  background: #0f0414;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}
